<template>
    <div>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                       <h4 name="header" class="modal-title">
                            Share Document <span v-if="generateJSONData.length==1" :title="generateJSONData[0].Name"> - {{ generateJSONData[0].Name }}</span>
                        </h4>
                        <button @click="closemanagepopup" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="modal-body">
                        <Form v-slot="{ values, errors }" ref="form">
                            <section id="divMainContent" class="main-content">
                                <div class="row">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left" style="width:43%">
                                        <div class="col-12 p-0 mb-2 d-flex">
                                            <span>Share with: </span>

                                            <!-- <input type="radio" id="Department" :checked="true" name="fav_language"
                                                value="Department" v-model="SharedType" class="mr-2"
                                                @change="onChange($event)">
                                            <label for="dep">Department</label>
                                           
                                            <input type="radio" id="User" @change="onChange($event)" name="fav_language"
                                                v-model="SharedType" value="User">&nbsp;
                                            <label for="user">Users</label>
                                            &nbsp;
                                            <input type="radio" id="Project" name="fav_language" value="Project"
                                                v-model="SharedType" @change="onChange($event)">&nbsp;
                                            <label for="project">Project</label>
                                            &nbsp;
                                            <input type="radio" id="Group" name="fav_language" value="Group"
                                                v-model="SharedType" @change="onChange($event)">&nbsp;
                                            <label for="">Groups</label>
                                            <a class="round-icon-small btn-dark theme-primary float-right"
                                                @click="OpenAddGroupPopup"><em             class="fa fa-plus text-white pt-7 font-18"></em> </a> -->

                                            <div class="ml-3 custom-control custom-radio custom-control-inline">
                                                <input type="radio" value="Department" v-model="SharedType"
                                                    class="form-check-input custom-control-input"
                                                    id="Department" name="Department"><label
                                                    class="custom-control-label" for="Department">Department</label>

                                            </div>

                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" value="User" 
                                                    v-model="SharedType" class="form-check-input  custom-control-input"
                                                    id="User" name="User"><label class="custom-control-label"
                                                    for="User">Users</label>
                                            </div> 
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" value="Project" v-model="SharedType"
                                                    class="form-check-input  custom-control-input" id="Project"
                                                    name="project"><label class="custom-control-label"
                                                    for="Project">Project</label>
                                            </div>
                                            <div class="custom-control custom-radio custom-control-inline">
                                                <input type="radio" value="Group" v-model="SharedType"
                                                    class="form-check-input  custom-control-input" id="Group"
                                                    name="group"><label class="custom-control-label"
                                                    for="Group">Groups</label>
                                            </div>
                                            <div class="custom-control custom-radio custom-control-inline pl-0">
                                                <a title="Add Groups"
                                                    class="round-icon-small2 btn-dark theme-primary float-right"
                                                    @click="OpenAddGroupPopup"><em class="fa fa-plus text-white font-14 mt-1 "></em> </a>

                                                
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="form-group" v-if="IsShowDiv">
                                    <label>{{ LabelName }}: <span class="text-danger">*</span></label>
                                    <Field :name="LabelName" :rules="{ 'required': true }" label="Parent"
                                        v-slot="{ errors }">  
                                        <select v-model="ParentValue" name="ParentValue"
                                            v-on:change="UsersByParentId(ParentValue)" :disabled="isDisabled"
                                            v-bind:class="{ 'form-control': true, 'is-invalid':errors[0] && !ParentValue}">
                                            <option value="" disabled selected="Selected" >{{ $t('Select') }}</option>
                                            <option v-for="item in UserParentList" v-bind:value="item.Id" >
                                                {{ item.Name }}
                                            </option>
                                        </select>
                                        <span class="text-danger" Style="font-size: 80%;">{{errors[0]}}</span>
                                        <!-- <span name="ParentValue" v-if="meta.touched && !meta.valid"
                                            class="invalid-feedback">{{ errorMessage }}</span> -->
                                    </Field>

                                </div>

                                <div class="row">
                                    <div class="col-lg-6" v-if="chkValidate == true">
                                        <div class="form-group">
                                            <label>Users: <span class="text-danger"
                                                    v-if="IsRequiredDiv">*</span></label>
                                            <Field name="SelectedValue" v-model="SelectedValue"
                                                :rules="{ 'required': true }" label="Users"
                                                v-slot="{ errors}">
                                                <Multiselect v-model="SelectedValue" :multiple="true" :options="Options"  :limit="1" :limitText="count => `and ${count} more`" 
                                                    
                                                    :close-on-select="false" :searchable="true"  :taggable="true" label="label" track-by="value"
                                                    placeholder="Select">
                                                    <!-- <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template> -->
                                                </Multiselect>
                                                <span name="SelectedValue" id="SelectedValue" 
                                                    class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-lg-6" v-if="chkValidate == false">
                                        <div class="form-group">
                                            <label>Users: <span class="text-danger"
                                                    v-if="IsRequiredDiv">*</span></label>
                                            <Multiselect v-model="SelectedValue" :options="Options" :limit="1" :limitText="count => `and ${count} more`"
                                                 :close-on-select="false" :searchable="true" :multiple="true" 
                                                :taggable="true" placeholder="All" track-by="value" :object="true" label="label"  :preselect-first="true">
                                                <!-- <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template> -->
                                            </Multiselect>

                                        </div>
                                    </div>
                                    <div class="col-lg-6">
                                        <div class="form-group">
                                            <label>Permissions: <span class="text-danger">*</span></label>
                                            <Field name="Permission" 
                                                :rules="{ 'required': true }" label="Permission"
                                                v-slot="{ errors }">
                                                <select v-bind:class="{ 'form-control': true, 'is-invalid': errors[0]}"  placeholder="Select" v-bind="field"
                                                    v-model="PermissionId">
                                                    <option value="" disabled>Select</option>
                                                    <option value="r">Read</option>
                                                    <option value="w">Write</option>
                                                    <option value="o">Owner</option>
                                                </select>
                                                <span name="Permission" id="PermissionId" v-if="errors[0]"
                                                    class="invalid-feedback d-block">{{ errors[0] }}</span>
                                            </Field>
                                        </div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-lg-6 d-flex align-items-center">
                                            <label> Expiration:&nbsp</label>
                                            <!-- <label class="switch">
                                                <input @click="userLoggedIn = !userLoggedIn"  id="isTrueCheck"
                                                    @change="ChangeCheckValue" type="checkbox">
                                                <span class="slider round">
                                                    <span  class="slider-yes">Yes</span>
                                                    <span class="slider-no">No</span>
                                                </span>
                                            </label> -->
                                            <label
                                                v-bind:class="{ 'switch': true, 'switch enabled': (isTrueCheck == true) }">
                                                <input type="checkbox" @click="userLoggedIn = !userLoggedIn"
                                                    v-bind:checked="isTrueCheck" v-bind:id="'isTrueCheck'"
                                                    v-on:change="ChangeCheckValue(item, $event)"
                                                    class="form-check-input rdoticketfor custom-control-input dynamic">
                                                <span class="slider round">
                                                    <span class="slider-yes">Yes</span>
                                                    <span class="slider-no">No</span>
                                                </span>
                                            </label>
                                    </div>
                                    <!-- <div class="col-lg-6">
                                        <div class="d-flex align-items-center">
                                            <label>Generate Link:&nbsp</label>
                                            <label class="switch">
                                                <input @click="userLoggedIn2 = !userLoggedIn2" @change="ChangeCheck(item, $event)"
                                                    id="isCheck" type="checkbox"
                                                    class="form-check-input rdoticketfor custom-control-input dynamic">
                                                <span class="slider round">
                                                    <span class="slider-yes">Yes</span>
                                                    <span class="slider-no">No</span>
                                                </span>
                                            </label>
                                        </div>
                                    </div> -->

                                    <div class="col-lg-6">
                                        <v-date-picker v-if="userLoggedIn" :min-date='new Date()' :popover="popover"
                                            v-model="ExpirationDate" class="input-group" placeholder="Enter expiration date">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input class="form-control" placeholder="Enter expiration date" :value="inputValue" v-on="inputEvents" />
                                                <div @click="togglePopover()" class="input-group-append"><span
                                                        class="input-group-text"><em class="fa fa-calendar"></em> </span>
                                                </div>
                                            </template>
                                        </v-date-picker>
                                    </div>
                                    <div v-if="userLoggedIn2" class="col-lg-6">
                                        <div class="input-group">
                                            <input class="form-control" ref="myText" disabled v-model="FileGenerateLink"
                                                v-on="inputEvents" />
                                            <div class="input-group-prepend">
                                                <span class="input-group-text">
                                                    <a class="fa fa-clone" @click="CopyLink"></a>
                                                </span>
                                            </div>
                                            <span v-show="tool" class="tooltiptext">{{ text }}</span>

                                        </div>
                                    </div>
                                </div>
                            </section>
                        </Form>
                    </div>

                    <div class="col-lg-12 float-left">
                        <div class="row flex-row-reverse">
                            <div class="col-lg-6 text-right py-3">
                                <button title="Share" @click="ShareDocument()"
                                    class="popupgreenbtn btn btn-primary formbtn widthhalf pr-2"><em class="fa fa-share-alt pr-2" aria-hidden="true"></em>  Share</button>
                                <a title="Cancel" @click="closemanagepopup"
                                    class="popupredbtn btn btn-danger formbtn widthhalf pr-2 ml-2">
                                    <em aria-hidden="true" class="fa fa-times pr-2"></em> Cancel</a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-3">
                                    <span class="mandatory">{{ $t("MandatoryString") }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- <div class="modal-footer">
                        <div class="col-lg-6">
                                    <div class="py-3">
                                        <span class="mandatory">{{$t("MandatoryString")}}</span>
                                    </div>
                                </div>
                        <button title="Share" @click="ShareDocument()"
                            class="popupgreenbtn btn btn-primary formbtn widthhalf pr-2"><em class="fa fa-share-alt pr-2"
                                aria-hidden="true"></em>  Share</button>
                        <button title="Cancel" @click="closemanagepopup"
                            class="popupredbtn btn btn-danger formbtn widthhalf pr-2">
                            <em aria-hidden="true" class="fa fa-times pr-2"></em> Cancel</button>
                    </div> -->
                </div>
            </div>
        </div>
    </div>
    <ModalAddGroup @ModalAddGroup="ModalAddGroup" @CloseAddGroupPopup="CloseAddGroupPopup" v-if="ShowModalAddGroup">
    </ModalAddGroup>
</div>
</template>

<style>
.multiselect-tags{
margin: 0px!important;
}
</style>

<script>
import DataService from "../../services/DataService"
import ModalAddGroup from "./Modal/ModalAddGroup.vue"
import Multiselect from 'vue-multiselect'
const docstorage = process.env.VUE_APP_DOCSTORAGE_API;
export default {
    components: {
        ModalAddGroup,
        Multiselect
    },
    data() {       
        return {
            isLoading:false,
            loggeduserid: null,
            companyId: null,
            sharemodal: false,
            foldername: null,
            folderIdmanage: null,
            currentState: false,
            datemodrl: new Date(),
            userLoggedIn: false,
            userLoggedIn2: false,
            isValid: '',
            popover: {
                visibility: 'click'
            },
            UserParentList: [],
            UserListDat: [],
            SelectedValue: [],
            Options: [],
            IsShowDiv: true,
            LabelName: 'Department',
            myObj: [],
            SelectValue: [],
            FileFolderValues: [],
            ExpirationChecked: 0,
            iSLinkGenerateChecked: 0,
            ExpirationDate: null,
            FileGenerateLink: null,
            vSelectValidate: false,
           // ParentValue: null,
            IsRequiredDiv: false,
            UserListArray: [],
            chkValidate: false,
            IsBindEdited: true,
            AddEditText: '',
            isDisabled: false,
            isTrue: true,
            ShowModalAddGroup: false,
            UniqueUrl: '',
            tool: false,
            text: "Copied!",
            PermissionId:'',
            ParentValue:'',
            UniqueCode:'',
            generateJSONForSingleFile:[],
            errorMessage:'',
            SharedType:'Department'
        }
    },
    watch: {
        SharedType:function(val){
            var vm=this;
            vm.onChange(); 
        }
    },
    computed: {
        isActive() {
            return this.currentState;
        }
    },
    props: {
        folderId: {
            type: String,
            required: true
        },

        folderName: {
            type: String,
            required: true
        },
        idFile: {
            type: String,
            required: true
        },
        docCode: {
            type: String,
            required: true
        },
        GroupData: {

        },
        StorageUniqueCode:
        {
            type: String,
            required: true
        },
        generateJSONData:
        {
            type:Array,
            required:true
        }

    },
    mounted: function () {
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $(".folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
    },
    created: function () {
        
        var vm = this;
        vm.loggeduserid = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.BindDepartmentDDL('Department');
        if (vm.GroupData != null) {
            vm.BindGroupsData(vm.GroupData);
        }
    },
    methods: {

limitTextFun(count){
return `and ${count} more`;
},
        //Binding users data in edit 
        BindGroupsData: function (GroupData) {
            
            var vm = this;
            vm.AddEditText = "Edit";
            vm.isDisabled = true
            if (vm.GroupData[0].Id != null) {
                vm.ParentValue = vm.GroupData[0].Id,
                    vm.ExpirationDate = vm.GroupData[0].view_expiration_date == null ? vm.GroupData[0].view_expiration_date : vm.GroupData[0].view_expiration_date.format("YYYY-MM-DD"),
                    vm.IsExpiration = vm.GroupData[0].IsExpiration;
                if (vm.IsExpiration == 1) {
                    vm.isTrueCheck = true;
                    vm.userLoggedIn = true
                    vm.ExpirationChecked = 1;
                }
                else {
                    vm.isTrueCheck = false;
                    vm.userLoggedIn = false
                    vm.ExpirationChecked = 0;
                }
                vm.PermissionId = vm.GroupData[0].permission
                if(vm.GroupData[0].SharedMembers!=null)
                {
                    vm.SelectedValue = vm.GroupData[0].SharedMembers.split(',').map(item => item.trim());
                }
                ;
                vm.SharedType = vm.GroupData[0].SharedType;
                if (vm.IsBindEdited == true) {
                    vm.BindUsersDDL(vm.SharedType, vm.ParentValue);
                    vm.BindDepartmentDDL(vm.SharedType);
                }
                vm.IsShowDiv = true;
                vm.IsRequiredDiv = false;
                if (vm.SharedType == "UserGroups") {
                    vm.LabelName = "Groups";
                    $("#Department").prop("checked", false);
                    $("#Group").prop("checked", true);

                }
                else if (vm.SharedType == "Project") {
                    vm.LabelName = "Project";
                    $("#Department").prop("checked", false);
                    $("#Project").prop("checked", true);
                }
                vm.chkValidate = false;
            }
        },

        UsersByParentId(ParentValue) {
            var vm = this;

            if (vm.LabelName == "Department") {
                vm.BindUsersDDL('Department', vm.ParentValue);
            }
            if (vm.LabelName == "Project") {
                vm.BindUsersDDL('Project', vm.ParentValue);
            }
            if (vm.LabelName == "Groups") {
                vm.BindUsersDDL('UserGroups', vm.ParentValue);
            }
            if (vm.LabelName == "Users") {
                vm.BindUsersDDL('Users', vm.ParentValue);
            }

        },
        OpenAddGroupPopup: function () {
            
            var vm = this;
            if (vm.AddEditText != "Edit") {
                vm.$parent.ModalAddGroup();
                vm.$parent.closePopupGroupListing();
            }
            else {
                this.ShowModalAddGroup = true;
                //vm.$emit('losepopup');
            }
        },
        onChange: function () {
            var vm= this;
            this.SeletcedValue = [];
            this.ParentValue='';
            if (vm.SharedType== "Department") {
                this.BindDepartmentDDL('Department');
                this.IsShowDiv = true;
                this.IsRequiredDiv = false;
                this.LabelName = "Department";
                this.chkValidate = false;
            }
            else if (vm.SharedType == "User") {
                ;
                this.BindUsersDDL('Users');
                this.IsShowDiv = false;
                this.IsRequiredDiv = true;
                this.UserParentList = null;
                this.LabelName = "Users";
                this.ParentValue = 0;
                this.chkValidate = true;

            }
            else if (vm.SharedType == "Project") {
                
                this.BindDepartmentDDL('Project');
                this.IsShowDiv = true;
                this.IsRequiredDiv = false;
                this.LabelName = "Project";
                this.chkValidate = false;
            }
            else if (vm.SharedType == "Group") {
                
                this.BindDepartmentDDL('UserGroups');
                this.LabelName = "Groups";
                this.IsShowDiv = true;
                this.IsRequiredDiv = false;
                this.chkValidate = false;
            }
        },
        popup: function () {
            this.sharemodal = true;
        },
        ShareDoc: function () {
            this.sharemodal = true;
        },
        closemanagepopup: function () {
            var vm = this;
            if (vm.AddEditText != "Edit") {
                vm.$parent.closePopupGroupListing();
            }
            else {
                vm.$parent.ClosePopupShared();
            }
        },
        CloseAddGroupPopup: function () {
            this.ShowModalAddGroup = false;
        },
        closeSharePopup: function () {
            var vm = this;
            vm.sharemodal = false;
        },
        BreadCrumb: function () {
            var vm = this;
            vm.$emit("BreadCrumb");
        },
        // This method is used to change checkbox values on click of Expriration date
        ChangeCheckValue: function (itm, event) {
            var x = $("#isTrueCheck").is(":checked");
            if (x == true) {
                this.ExpirationChecked = 1;
            }
            else {
                this.ExpirationChecked = 0;
                this.ExpirationDate = null;
            }

        },
        // This method is used to change checkbox values on click of generate link
        ChangeCheck: function (item) {
            
            var vm = this;
            var params='';
            var x = $("#isCheck").is(":checked");
            if(x==true)
            {
                if(vm.generateJSONData!=null)
                {
                    params = `docJsonData=${JSON.stringify(vm.generateJSONData)}`;
                    
                }
                else{
                    let obj = {
                        SourceId: vm.idFile,
                        RefCode: vm.docCode
                    }
                    vm.generateJSONForSingleFile.push(obj);
                    params = `docJsonData=${JSON.stringify(vm.generateJSONForSingleFile)}`;
                }
            DataService.GenerateDocPublicLink(params).then((response) => {
                
                if(response.data.code==1)
                {
                    
                    vm.UniqueUrl = docstorage + '/' + 'GenerateDocPublicLink?storageUniqueCode=' + response.data.docUniqueCode;  
                    vm.FileGenerateLink = vm.UniqueUrl;
                    // vm.ShowAlert("Generate link has been successfully generated");
                    vm.isLoading=false;
                }
            });
        }
         
            else {
                vm.iSLinkGenerateChecked = 0;
                vm.FileGenerateLink = null;

            }
        },
        GeneratePublickLink: function () {
            
            var vm = this;
            var params = `docId=${vm.folderId}&refCode=DocFolder`;
            DataService.GenerateDocPublicLink(params).then((response) => {
                if(response.data.code==1)
                {
                    vm.UniqueCode=response.data.docUniqueCode;
                    vm.UniqueUrl = docstorage + '/' + 'GetPublicallyStorageDocs?storageUniqueCode=' + vm.UniqueCode;  
                    vm.FileGenerateLink =  vm.UniqueUrl;
                }
            });
        },
        // Bind department list on click of radiobuttion of department
        BindDepartmentDDL: function (refid, ParentValue) {
            var vm = this;
            vm.isLoading=true;
            vm.Options = []
            // Params for api hit
            let params = {};
            params["refCode"] = refid
            params["parentId"] = ParentValue

            // var params =`refCode=${vm.refid}`
            DataService.SharedUsersParentList(params).then(function (response) {
                if (response.data != null) {
                    vm.UserListData = JSON.parse(response.data);
                    vm.UserParentList = vm.UserListData.DocParentListing;
                }
                else{
                    vm.UserParentList=[];
                }
                vm.isLoading=false;
            });
          
        },


        //Bind Project list ddl
        BindUsersDDL: function (refid, ParentValue) {
            var vm = this;
            vm.Options = []
            // Params for api hit
            let params = {};
            params["refCode"] = refid,
                params["parentId"] = ParentValue,
  
                DataService.SharedUsersParentListByParentId(params).then(function (response) {
                    vm.UserListData = JSON.parse(response.data);
                    vm.UserList = vm.UserListData.DocParentUserListing;
                    if (vm.UserList.length != null) {
                        vm.UserList.forEach((item, index) => {
                            vm.Value = item.Id;
                            vm.first_value = item.Name
                            vm.Options.push({
                                value: vm.Value,
                                label: vm.first_value
                            })
                        });
                        if (vm.GroupData != null) {
                            vm.BindGroupsData(vm.GroupData);
                            vm.IsBindEdited = false;
                        }
                    }
                });

        },
        showTool() {
            this.tool = true
            setTimeout(() => this.tool = false, 1000)
        },
        // This method is used for copy link to clipboard
        CopyLink() {
            var vm = this;
            navigator.clipboard.writeText(vm.FileGenerateLink);
            vm.showTool()
        },
        // save shared documnet method
        ShareDocument: function (values) {
            var vm = this;
            vm.isLoading=true;
            var sourcevalues = "";
            var IsDocCode = "";
            var folderId = 0;
            vm.myObj=[];
            // vm.$refs.form.setFieldTouched('SelectedValue', true);
            // vm.$refs.form.setFieldTouched('PermissionId', true);
            
            vm.$refs.form.validate().then((result) => {
                vm.vSelectValidate = true;
            if((vm.ParentValue > 0 && vm.SharedType!='User') || (vm.ParentValue ==0 && vm.SharedType=='User'))
            {
                vm.errorMessage='';
                $("#ParentValue").removeClass("is-invalid");
            
                
                if (result) {
                // if (vm.ParentValue != null && vm.PermissionId != null) {
                  // if (vm.SelectedValue.length > 0 || vm.IsShowDiv == true) {
                        if (vm.idFile != null) {

                            // --------
                            this.IsDocCode = this.docCode;
                            sourcevalues = this.idFile;
                            folderId=this.folderId;
                            if (vm.UserParentList != null) {
                                var ParentId = vm.ParentValue;
                            }
                            else {
                                ParentId = 0;
                            }
                            let obj1 = {};
                            if (vm.SelectedValue.length == 0) {
                                obj1 = {
                                    SourceId: sourcevalues,
                                    UserId: 0
                                }
                                vm.SelectValue.push(obj1)
                            }
                            else {
                                vm.SelectedValue.forEach((item, index) => {

                                    obj1 = {
                                        SourceId: sourcevalues,
                                        UserId: item.value ?? 0
                                    }
                                    vm.SelectValue.push(obj1)

                                });
                            }
                            vm.FileFolderValues = [...new Set(vm.SelectValue)]


                            if (sourcevalues != null) {
                                let obj = {
                                    SourceId: sourcevalues,
                                    DocPermission: vm.PermissionId,
                                    ParentId: ParentId,
                                    RefCode: this.IsDocCode,
                                    IsExpiration: vm.ExpirationChecked,
                                    DocExpirationDate: vm.ExpirationDate,
                                    IsFileGenerateLink: vm.iSLinkGenerateChecked,
                                    FileGenerateLink: vm.FileGenerateLink == null ? null : vm.FileGenerateLink.split('/')[4],
                                    ShareUserIds: vm.FileFolderValues

                                }
                                vm.SelectValue = [];
                                vm.myObj.push(obj);
                            }
                        }
                        
                        else {
                            
                            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                                this.IsDocCode = $(this).attr("doccode")
                                folderId = $(this).attr("folderId") == undefined ? 0 : $(this).attr("folderId");
                                sourcevalues = $(this).val();
                                if (vm.UserParentList != null) {
                                    var ParentId = vm.ParentValue;
                                }
                                else {
                                    ParentId = 0;
                                }
                                let obj1 = {};
                                if (vm.SelectedValue.length == 0) {
                                    obj1 = {
                                        SourceId: sourcevalues,
                                        UserId: 0
                                    }
                                    vm.SelectValue.push(obj1)
                                }
                                else {
                                    vm.SelectedValue.forEach((item, index) => {

                                        obj1 = {
                                            SourceId: sourcevalues,
                                            UserId: item.value ?? 0
                                        }
                                        vm.SelectValue.push(obj1)

                                    });
                                }
                                vm.FileFolderValues = [...new Set(vm.SelectValue)]


                                if ($(this).attr("doccode") == 0 || $(this).attr("doccode") == 1) {
                                    let obj = {
                                        SourceId: sourcevalues,
                                        DocPermission: vm.PermissionId,
                                        ParentId: ParentId,
                                        RefCode: this.IsDocCode,
                                        IsExpiration: vm.ExpirationChecked,
                                        DocExpirationDate: vm.ExpirationDate,
                                        IsFileGenerateLink: vm.iSLinkGenerateChecked,
                                        FileGenerateLink: vm.FileGenerateLink == null ? null : vm.FileGenerateLink.split('/')[4],
                                        ShareUserIds: vm.FileFolderValues

                                    }
                                    vm.SelectValue = [];
                                    vm.myObj.push(obj);
                                }
                            });
                        }

                        var jsonObj = JSON.stringify(vm.myObj)
                        var params = `jsonData=${jsonObj}&folderId=${folderId==""?0:folderId}&parentType=${vm.SharedType}`;
                        DataService.ManageSharedDocStorage(params).then(function (response) {
                            
                            if (response.data != null) {
                                if (response.data == 1) {
                                    vm.ShowAlert(vm.$t('ShareDocumentSuccess', [vm.$t('ShareFileFolder')]), "Success", true, vm.$t("Alert"));
                                    if (vm.AddEditText != "Edit") {
                                        vm.$parent.closepopup();
                                        vm.$emit("StorageDocumentListing");
                                         $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                        vm.$parent.GetStorageFileSummary(vm.idFile);
                                        
                                    }
                                    else {
                                        
                                        vm.$parent.ClosePopupShared();
                                        vm.$parent.FetchData();
                                    }
                                }
                                else {
                                    vm.ShowAlert(vm.$t('ShareDocumentError', [vm.$t('ShareFileFolder')]), "failure", true, vm.$t("Alert"));
                                }
                            }
                        });
                   } 
            }
            else{
                if(vm.SharedType=="Department"){
                    vm.ShowAlert("Department is required", "failure", true, vm.$t('Alert'));
                     $("#ParentValue").addClass("is-invalid");
                }
                else if(vm.SharedType=="Project")
                {
                    vm.ShowAlert("Project is required", "failure", true, vm.$t('Alert'));
                     $("#ParentValue").addClass("is-invalid");
                }
                else if(vm.SharedType=="Group")
                {
                    vm.ShowAlert("Group is required", "failure", true, vm.$t('Alert'));
                     $("#ParentValue").addClass("is-invalid");
                }
               
                vm.isLoading=false;
                return false;
                }
                
                    vm.isLoading=false;
           
            });
            
        }
    }
}
</script>
