<!-- Add group pupup template -->
<template v-for="item in GroupsData">

    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <!-- <h4 name="header" class="modal-title" v-if="AddEditText==='Edit'">{{$t('Edit Group')}}</h4> -->
                        <h4 name="header" class="modal-title" >Add Group</h4>
                        
                        <button @click="ClosePopup" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="modal-body">
                        <Form v-slot="{ values, errors }" ref="form">
                            <section id="divMainContent" class="main-content">
                                    <div class="row">
                                        <div class="col-lg-4">
                                            <div class="form-group">
                                                <label>{{ $t('Group Name') }}:<span class="text-danger">*</span></label>
                                                <Field name="Group Name" v-model="txtGroupName"
                                                    :rules="{ 'required': true }" label="Group Name"
                                                    v-slot="{ errors }">
                                                    <input v-bind="field" id="txtGroupName"
                                                        :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                        type="text" maxlength="200" v-model="txtGroupName" />
                                                    <span name="txtGroupName" id="txtGroupName"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-lg-4">
                                            <div class="form-group" id="app">
                                        
                                                <label>{{ $t('Select User(s)') }}:<span class="text-danger">*</span></label>
                                                 <Field name="SelectedValue" v-model="SelectedValue" :rules="{ 'required': true }" label="Users" v-slot="{  field, errorMessage, meta }">
                                                    <Multiselect v-model="SelectedValue" :limit="1" :limitText="count => `and ${count} more`" :options="Options" :multiple="true"  mode="tags" :close-on-select="false"
                                                    v-bind="field"  :searchable="true" :taggable="false" track-by="value" :object="true" :preselect-first="true" label="label"  @tag="addTag">
                                                    </Multiselect>
                                                    <span name="SelectedValue" id="SelectedValue"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                            <div class="form-group">
                                                <label>{{ $t('Description') }}:<span
                                                        class="text-danger">*</span></label>
                                                <Field name="Description" v-model="txtDescription"
                                                    :rules="{ 'required': true }" label="Description"
                                                    v-slot="{ errors }">
                                                    <textarea v-bind="field"
                                                        :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                        id="txtDescription" maxlength="1000"
                                                        v-model="txtDescription"></textarea>
                                                    <span name="txtDescription" id="txtDescription"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field>


                                            </div>
                                        </div>
                                    </div>
                            </section>
                        </Form>
                    </div>
                    <div class="modal-footer">
                        <button title="Search" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                            v-on:click="Save(values, meta)">Save</button>
                        <button title="Cancel" @click="ClosePopup"
                            class="popupredbtn btn btn-danger formbtn widthhalf pr-2"><em aria-hidden="true"
                                class="fa fa-times pr-2"></em> Cancel</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import ModelAddGroupDataService from "@/services/DataService";
import Multiselect from 'vue-multiselect'
export default {
    components:{
        Multiselect
    },
    props:
    {
        GroupData: {

        }
        // GroupUserIds: object

    },
    data() {
        return {
            isLoading: false,
            loggeduserid: null, //Get logged in user info
            companyId:null, // Get company id
            selected: [],
            UserListDDL: [], //Used for bind all dropdown values
            first_value: "",
            Options: [],
            closeOnSelect: false,
            ShowModalAddGroup: false,
            SelectedValue: [],
            AddEditText: '',
            SelectValue: [],
            GroupId: 0

        }
    },
    created: function () {
        var vm=this;
        vm.loggeduserid=vm.GetUserInfo.ID;
        vm.companyId=vm.GetUserInfo.CompanyId;
        vm.BindUserListDDL('', 'COMPANY_USER_LIST');

    },
    methods: {
        //Binding users data in edit 
        BindGroupsData: function (GroupData) {
            this.AddEditText = "Edit"; 
            if (this.GroupData.GroupId != null) {
                this.GroupId = this.GroupData.GroupId,
                    this.txtGroupName = this.GroupData.txtGroupName,
                    this.txtDescription = this.GroupData.txtDescription,
                    this.SelectedValue = this.GroupData.GroupUserIds.split(',').map(item => item.trim());
            }
        },
        limitTextFun(count){
        return `and ${count} more`;
        },
        // Used for close modal popups 
        ClosePopup: function () {
            
            this.$emit("CloseAddGroupPopup");
            this.$parent.SharePopup();
        },
        // Dropdown binding
        BindUserListDDL: function (refid, type) {
            
            var vm = this;
            vm.isLoading=true;
            // Params for api hit 
            let params = {};
            params["type"] = type;
            params["refid"] = refid
            ModelAddGroupDataService.GetDDLData(params).then(function (response) {
                debugger
                if (response.data != null) {
                    vm.UserListDDL = response.data.DATA;
                    // Options: response.data.DATA;
                    if(response.data!=''){
                        response.data.DATA.forEach((item, index) => {
                        vm.Value = item.value;
                        vm.first_value = item.first_value
                        vm.Options.push({
                            value: vm.Value,
                            label: vm.first_value
                        })
                    });
                    }
                    
                    if (vm.GroupData != null) {
                        vm.BindGroupsData(vm.GroupData);
                    }
                    vm.isLoading=false;
                }
            });
        },
        CloseAddGroupPopup: function () {
            this.ShowModalAddGroup = false;
        },
       
        // Used for save group data
        Save: function (values) {
            debugger
            var vm = this;
            // vm.$refs.form.setFieldTouched('txtGroupName', true);
            // vm.$refs.form.setFieldTouched('txtDescription', true);
            // vm.$refs.form.setFieldTouched('SelectedValue', true);
            vm.$refs.form.validate().then((result) => {
                
                vm.vSelectValidate = true;
                if (result) {
                    let obj1 = {};
                    vm.SelectedValue.forEach((item, index)=>{
                       vm.SelectValue.push(item.value)
                    })
                    if(vm.SelectedValue.length > 1)
                    {
                        let fullJson = {
                            "GroupId": vm.GroupId,
                            "Name": vm.txtGroupName,
                            "Description": vm.txtDescription,
                            "Module_Id": '0',
                            "Sub_Module_Id": '0',
                            "Company_Id": vm.companyId,
                            "Created_By": vm.loggeduserid,
                            "GroupUserIds": vm.SelectValue.join(',')
                        };
                        
                        
                        var objectPostString = JSON.stringify(fullJson);
                        ModelAddGroupDataService.ManageUserGroup(objectPostString).then((response) => {
                            vm.isLoading=true;
                            if (response.statusText == "OK") {
                            vm.$emit('CloseAddGroupPopup');
                            if (vm.AddEditText == "Edit") {
                                vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('GroupName')]), "Success", true, vm.$t("Alert"));
                            }
                            else {
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('GroupName')]), "Success", true, vm.$t("Alert"));
                            }
                            vm.isLoading=false;
                        }
                        else {
                            vm.$emit('CloseAddGroupPopup');
                            vm.ShowAlert(vm.$t('Error' + [vm.$t('GroupName')]), "failure", true, vm.$t("Alert"));
                            vm.isLoading=false;
                        }
                    });
                }
                else{
                    vm.ShowAlert([vm.$t('Select more then one user.')]), "failure", true, vm.$t("Alert");
                }
                }
                vm.isLoading=false;
            });
        }
    }
}


</script>