<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h4 name="header" class="modal-title">
                            Choose Destination Folder
                        </h4>
                        <button @click="closepopuptag" class="close"><em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div id="main" class="collapse show px-1 py-1 border FullTreeData"
                            style="max-height:90vh; overflow-y: auto;">
                            <DocCommonTreeView :checkActionType="checkActionType" :mySpaceOrOrganization="mySpaceOrOrganization" :organizationSpace="organizationSpace"  :type="checkType" :MySpaceFolders="FolderArray" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"></DocCommonTreeView>                          
                        </div>
                    </div>
                    <div class="modal-footer text-right">
                        <button title="Search" @click="MoveFolderFiles" v-if="checkActionType == 'Move'"
                            class="popupgreenbtn btn btn-success formbtn widthhalf"><em class="fa fa-arrows"></em>
                            Move</button>
                        <button title="Search" class="popupgreenbtn btn btn-success formbtn widthhalf"
                            @click="CloneFolderFiles" v-if="checkActionType == 'Copy'"><em class="fa fa-files-o"></em>
                            Clone</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DocCommonTreeView from "../../../components/DocCommonTreeView.vue";
import DataService from "./../../../services/DataService";
import ChildFolder from "./.././../../components/ChildFolder.vue"
export default {
    components: {
    ChildFolder,
    DocCommonTreeView
},
    props: {
        // FolderIdMoveLeftMenu: {
        //     type: Function,
        //     required: true
        // },
        checkActionType: {
            type: String,
            required: false
        },
        moveObj: {
            type: Array,
            required: true,
        },
        organizationSpace:{
            type: Array,
            required: true,
        },
        FolderArray:{
            type: Array,
            required: true,
        },
        mySpaceOrOrganization:{
            type: Number,
            required: false
        }
    },
    data() {
        return {

            isLoading: false,
            loggeduserid: null,
            //folderarray: [],
            FindalFolderArray: [],
            checkType:'popup'
        }
    },
    mounted: function () {
        console.log($("#FolderFullTree"))
        $("#FolderFullTree").last().treeview();
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $("#FolderFullTree .folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
    },
    created: function () {
        this.loggeduserid = this.GetUserInfo.ID;
        this.GetFullTree();
    },
    methods: {
        GetFullTree() {            
            var vm = this;
            vm.isLoading=true;
            var params = JSON.stringify(vm.moveObj);
            DataService.MoveFolderTreeView(params).then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    //vm.sharedfolders(value)
                    if (value.OWNER_USER_ID == this.loggeduserid && value.IsGlobal == 0) {
                        var CheckIfExist = vm.FindalFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getChildFolder(folderId, value, response.data.folderTreeList)
                                vm.FindalFolderArray.push(rslt)
                            }
                        }

                    }

                });
                vm.folderTreeList = response.data.folderTreeList;
               // vm.SharedUserList = response.data.userList;
                vm.sharedFolder = response.data.sharedTreeList;
                vm.folderTreeList.forEach((value, index) => {
                    vm.sharedFolder.forEach((item, index) => {
                        if (item.ParentId != value.FOLDER_ID && value.SHARED_USER_ID == this.loggeduserid) {
                            // vm.abc.push(value);
                            // vm.SharedFolderParent=[...new Set(vm.abc)]
                        }
                    })
                })
                vm.isLoading = false;
            });
        },
        getChildFolder: function (folderId, currentItem, data) {
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        FolderIdMoveLeftMenu(data, parentid) {
            debugger
            var vm = this;
            vm.FolderIds = data;
            vm.$emit('FolderIdmove', data, parentid);
        },
        MoveFolderFiles: function () { 
                       
            var vm = this;
            vm.$emit("MoveFoldersFiles");
            vm.$emit("closemovefilefolderpopup");
        },
        CloneFolderFiles: function () {

            var vm = this;
            vm.$emit("CloneFoldersFiles");
            vm.$parent.closemovefilefolderpopup();
        },
        closepopuptag: function () {

            var vm = this;
            vm.$emit("closemovefilefolderpopup");
            vm.$parent.closepopupFolder();
        }
    }
}
</script>