<template>
        <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <!-- <h4 name="header" class="modal-title" v-if="AddEditText==='Edit'">{{$t('Edit Group')}}</h4> -->
                        <h4 name="header" class="modal-title" >Copy Confirmation</h4>                        
                        <button @click="ClosePopup" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="modal-body">
                        <form id="frmCopyMoveConfirmation">       
        <div id="divPopUp">
            <input type="hidden" id="hdnDestFolderId" :value="destFolderId" />
            <input type="hidden" id="hdnOperationSelected" :value="operationSelected" />
            <div class="table-responsive tableborder">
                <table class="table-sorting table table-bordered mb-0">
                    <thead class="thead-light">
                        <tr>
                            <th width="5%" style="font-weight:bold" class="text-center">#</th>
                            <th width="45%" style="font-weight:bold">{{$t('FileName')}}</th>
                            <th width="15%" style="font-weight:bold">{{$t('Status')}}</th>
                            <th  width="35%" style="font-weight:bold">{{$t('Option')}}</th>
                        </tr>
                    </thead>
                    <tbody>
                      
                            <tr v-if="fileList==null ||fileList.length == 0">
                                <td class="text-center text-danger no-record" colspan="4">
                                    <span>{{$t('NoRecordfound')}}</span>
                                </td>
                            </tr>                                         
                                <tr v-for="(fileItem,index) in fileList" :key="index"
                                 :class="{'gray':(index % 2 == 0)}">
                                    <td class="td0 tdcm text-center">{{ index+1 }}</td>
                                    <td>
                                        <span :title="fileItem.fileName">{{fileItem.fileName}}</span>
                                    </td>
                                    <td>{{GetStatusName(fileItem.status)}}</td>
                                    <td>
                                            <div v-if="fileItem.confilict == true">
                                                
                                                <div class="form-check form-check-inline" v-if="GetStatusName(fileItem.status)!='Successful'">
                                                    
                                                     <input name="status" type="radio" v-model="radiobtnValue" value="2"/>
                                                    <label class="form-check-label font-weight-normal">
                                                       {{$t('Rename')}}
                                                    </label>
                                                </div>
                                               
                                                    <div v-if="GetStatusName(fileItem.status)!='Successful'" class="form-check form-check-inline" >
                                                        <input name="status" type="radio" v-model="radiobtnValue" value="3" />
                                                        
                                                        <label class="form-check-label font-weight-normal">
                                                          {{$t('Replace')}}
                                                        </label>
                                                    </div>
                                         
                                            </div>
                                            <div style="display: none">
                                                <div class="form-check form-check-inline">
                                                    <!-- @Html.RadioButtonFor(m => m[i].CopyMoveOption, TALYGEN.Areas.DocStorage.Models.CopyMoveOption.Cancel) -->
                                                    <label class="form-check-label font-weight-normal">
                                                      {{$t('Cancel')}}
                                                    </label>
                                                </div>
                                                <div class="form-check form-check-inline">
                                                    <!-- @Html.RadioButtonFor(m => m[i].CopyMoveOption, TALYGEN.Areas.DocStorage.Models.CopyMoveOption.Rename) -->
                                                    <label class="form-check-label font-weight-normal">
                                                        {{$t('Rename')}}
                                                    </label>
                                                </div>
                                                <div class="form-check form-check-inline">
                                                    <!-- @Html.RadioButtonFor(m => m[i].CopyMoveOption, TALYGEN.Areas.DocStorage.Models.CopyMoveOption.Replace) -->
                                                    <label class="form-check-label font-weight-normal">
                                                         {{$t('Replace')}}
                                                    </label>
                                                </div>
                                            </div>
                                       
                                    </td>
                                </tr>
                    </tbody>
                </table>
            </div> 
        </div>
       
    </form>
                    </div>
                    <div class="modal-footer" v-if="fileList && fileList[0].status!=1">
                        <button title="Search" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"
                            v-on:click="Save()">Save</button>
                        <button title="Cancel" @click="ClosePopup"
                            class="popupredbtn btn btn-danger formbtn widthhalf pr-2"><em aria-hidden="true"
                                class="fa fa-times pr-2"></em> Cancel</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    
</template>
<script>
export default {
  props:{
    copyFileInfo:null
  },
      data() {
        return {
          destFolderId:null,
          operationSelected:null,
          fileList:[]
        };
    },
    created(){
this.fileList=this.copyFileInfo;
console.log(this.fileList);
    },
    methods:{
GetStatusName(status){
    var statusName=''
    switch(status){
   case -1:
    statusName='InProcess';
    break;
    case 0:
        statusName='Pending';
    break;
    case 1:
    statusName='Successful';
    break;
    case 2:
    statusName='Failure';
    break;
    case 3:
    statusName='AlreadyExist';
    break;
    case 4:
    statusName='FileSizeExceeded';
    break;
    case 5:
    statusName='Uploading';
    break;
    }
    return statusName;
},
ClosePopup(){
    this.$parent.copyFileInfo=null;
    $("#upload").removeClass("disabled");
},
        Save() {
            if(this.fileList[0].confilict == true){
                let tempArray={}
                tempArray={
                    DestFolderId:this.fileList[0].destFolderId,
                    FileId:this.fileList[0].fileId,
                    FileName:this.fileList[0].fileName,
                    Operation:this.fileList[0].operation,
                    Status:this.fileList[0].status,
                    CopyMoveOption:this.radiobtnValue,
                    confilict:true
                }
               if(this.radiobtnValue==undefined){
                this.ShowAlert("Please select one option.","warning");
                return false
               }
                this.fileList=[]
                this.fileList.push(tempArray);
                this.$emit('SaveCopyFile',this.fileList);
                this.$parent.$parent.ShowUpload(true);
            }
            else{
                this.$emit('SaveCopyFile',this.fileList);
            }
            
        }
    }
}

</script>